import { useEffect, useState } from "react";
import { Card, Table, Tabs, Tab, Button } from "react-bootstrap"
import Paginationbar from "./Paginationbar";
import TableWritings from "./TableWritng";
import s from "./TableMain.module.css";
import writing from "./TableWritng";

function TableMain() {
    const [writings, setWritings] = useState({});
    const [key, setKey] = useState('all');

    useEffect(() => {
        if(key === "all")
            console.log("Send all request");
        if(key === "hot")
            console.log("Send hot request");
    }, [key])

    const date = new Date().toISOString().substring(0, 10);
    
    const fakeData = [
        { index : 1, title: "Fake Data", author: "Bandall", date: date, view: 100, recommand: 20, link: "www.naver.com" },
        { index : 2, title: "Fake Data2", author: "Bandall77", date: date, view: 200, recommand: 30, link: "www.naver.com" },
        { index : 3, title: "Fake Data2", author: "Bandall77", date: date, view: 200, recommand: 30, link: "www.naver.com" }
    ];
    
    return (
        <div className={s.wrap_inner}>
            <a href="/" className={s.table_header}>
                <h2> </h2>
            </a>
            <Tabs
                activeKey={key}
                onSelect={(k) => setKey(k)}
                id="uncontrolled-tab-example"
                className="mb-3"
            >
                <Tab eventKey="all" title="" />
                <Tab eventKey="hot" title="" />
            </Tabs>
            <Card>
                <Table hover className={s.table}>
                <thead>
                    <tr>
                        <th className={s.table_number}></th>
                        <th className={s.table_title}></th>
                        <th className={s.table_author}></th>
                        <th className={s.table_date}></th>
                        <th className={s.table_view}></th>
                        <th className={s.table_suggest}></th>
                    </tr>
                </thead>
                <tbody>
                    {fakeData.map((data) => {
                        return (
                            <TableWritings 
                                key={data.index}
                                index={data.index}
                                title={data.title}
                                author={data.author}
                                date={data.date}
                                view={data.view}
                                recommand={data.recommand}
                                link={data.link}
                                style={{"border-collapse":"inherit"}}
                            />
                        )
                    })}
                </tbody>
                </Table>
                <div className={s.search_bar}>
                    <div>
                        <input placeholder="" name="search"></input>
                        <Button variant="primary">
                            <a href="/search" style={{color: "white", textDecoration: "none"}}></a>
                        </Button>{' '}
                    </div>
                    <div>
                        <Button variant="primary">
                            <a href="/writeboard" style={{color: "white", textDecoration: "none"}}></a>
                        </Button>{' '}
                    </div>
                </div>
                <div className={s.paginationbar}>
                    <Paginationbar />
                </div>
            </Card>
        </div>
    )
}

export default TableMain;